
import React from 'react';
import { useAppContext } from '../hooks/useAppContext';

const Header: React.FC = () => {
    const { theme, toggleTheme, filters, setFilters } = useAppContext();

    const user = {
        name: 'Alê Portela',
        role: 'Gabinete Parlamentar',
        avatar: 'https://lh3.googleusercontent.com/aida-public/AB6AXuDUnjlvQbUg_zEBMBx1WgZuivL_NXChBw36Rgs-l5UHz5hpflxY_a2aKMGUwKosoGpp404av2T6DMZ4t6bLA7BSS9ZrSYH1SbLP_io8tDhOeTvsYgQc4Oma-vyjYtAjGWye2lE4lAoPaRb09p4wFG1ew8sVEkXF3pdUMxPsXnYvjgucsO1cflwlB8-Ur5w67Co8kHpkJgSjLyh3xwkmKADZn_3YJYYkrdn5Vcp5W8LRu4rwjMZ9iaTKT4Mxp7J6575OrXIcyBCNpUk1'
    };
    return (
        <header className="h-16 bg-white dark:bg-slate-800 border-b border-slate-200 dark:border-slate-700 flex items-center justify-between px-8 shrink-0">
            <div className="flex items-center gap-6 flex-1 max-w-4xl">
                <div className="relative w-full max-w-md">
                    <span className="absolute inset-y-0 left-0 flex items-center pl-3 text-slate-400">
                        <span className="material-symbols-outlined text-lg">search</span>
                    </span>
                    <input className="w-full pl-10 pr-4 py-2 bg-slate-100 dark:bg-slate-900/50 border-slate-200 dark:border-slate-700 rounded-lg text-sm focus:ring-2 focus:ring-primary/50 transition-all" placeholder="Buscar no Portela Hub..." type="text"/>
                </div>
                <div className="flex items-center gap-3 shrink-0">
                    <span className="text-xs font-semibold text-slate-500 uppercase tracking-tight">Macrorregião</span>
                    <select
                        value={filters.regiao}
                        onChange={(e) => setFilters(prev => ({ ...prev, regiao: e.target.value }))}
                        className="bg-slate-100 dark:bg-slate-900/50 border-slate-200 dark:border-slate-700 rounded-lg text-sm font-medium py-2 px-4 focus:ring-2 focus:ring-primary/50 appearance-none"
                    >
                        <option>Todas as Regiões</option>
                        <option>Metropolitana</option>
                        <option>Triângulo Mineiro</option>
                        <option>Norte de Minas</option>
                        <option>Zona da Mata</option>
                    </select>
                </div>
            </div>
            <div className="flex items-center gap-4">
                 <button onClick={toggleTheme} className="p-2 text-slate-500 dark:text-slate-400 hover:bg-slate-100 dark:hover:bg-slate-700 rounded-full transition-colors">
                    <span className="material-symbols-outlined">{theme === 'dark' ? 'dark_mode' : 'light_mode'}</span>
                </button>
                <button className="p-2 text-slate-500 dark:text-slate-400 hover:bg-slate-100 dark:hover:bg-slate-700 rounded-full transition-colors relative">
                    <span className="material-symbols-outlined">notifications</span>
                    <span className="absolute top-2 right-2 size-2 bg-red-500 rounded-full border-2 border-white dark:border-slate-800"></span>
                </button>
                <div className="h-8 w-px bg-slate-200 dark:bg-slate-700 mx-2"></div>
                <div className="flex items-center gap-3">
                    <div className="text-right">
                        <p className="text-sm font-bold leading-none text-navy-dark dark:text-white">{user.name}</p>
                        <p className="text-xs text-slate-500 dark:text-slate-400 leading-none mt-1">{user.role}</p>
                    </div>
                    <div className="size-9 rounded-full bg-slate-200 overflow-hidden border border-slate-300">
                        <img alt="User Profile" className="w-full h-full object-cover" src={user.avatar}/>
                    </div>
                </div>
            </div>
        </header>
    );
};
export default Header;
