
import React, { useState, useMemo } from 'react';
import { Eleitorado } from '../types';

interface EleitoradoCardProps {
    eleitoradoData: Eleitorado;
}

const EleitoradoCard: React.FC<EleitoradoCardProps> = ({ eleitoradoData }) => {
    const [cargo, setCargo] = useState('Deputada Estadual');
    const [ano, setAno] = useState(2022);

    const votos = useMemo(() => {
        return eleitoradoData.votos[ano]?.[cargo] ?? 0;
    }, [ano, cargo, eleitoradoData.votos]);
    
    const votosAnterior = useMemo(() => {
        const anoAnterior = ano === 2022 ? 2018 : 2020;
        return eleitoradoData.votos[anoAnterior]?.[cargo] ?? 0;
    }, [ano, cargo, eleitoradoData.votos]);

    const percentualVariacao = useMemo(() => {
        if (votosAnterior === 0) return 0;
        return ((votos - votosAnterior) / votosAnterior) * 100;
    }, [votos, votosAnterior]);


    return (
        <div className="bg-white dark:bg-slate-800 rounded-xl shadow-sm border border-slate-200 dark:border-slate-700 overflow-hidden">
            <div className="p-5 border-b border-slate-100 dark:border-slate-700 flex justify-between items-center">
                <h3 className="text-navy-custom dark:text-white text-lg font-bold">Dados do Eleitorado</h3>
                <span className="material-symbols-outlined text-primary">how_to_vote</span>
            </div>
            <div className="p-5 space-y-5">
                <div className="text-center pb-2">
                    <p className="text-[11px] font-bold text-slate-400 uppercase tracking-widest mb-0.5">Eleitorado Total</p>
                    <p className="text-2xl font-black text-navy-custom dark:text-white tracking-tight">{eleitoradoData.total.toLocaleString('pt-BR')}</p>
                </div>
                <div className="grid grid-cols-2 gap-3">
                    <div className="space-y-1">
                        <label className="text-[10px] font-bold text-slate-400 uppercase ml-1">Cargo</label>
                        <select onChange={(e) => setCargo(e.target.value)} value={cargo} className="w-full text-xs font-bold bg-slate-50 dark:bg-slate-700 border-slate-200 dark:border-slate-600 rounded-lg focus:ring-1 focus:ring-primary py-2 px-3 text-navy-custom dark:text-slate-200">
                            <option>Deputada Estadual</option>
                            <option>Deputada Federal</option>
                            <option>Governador</option>
                        </select>
                    </div>
                    <div className="space-y-1">
                        <label className="text-[10px] font-bold text-slate-400 uppercase ml-1">Ano</label>
                        <select onChange={(e) => setAno(Number(e.target.value))} value={ano} className="w-full text-xs font-bold bg-slate-50 dark:bg-slate-700 border-slate-200 dark:border-slate-600 rounded-lg focus:ring-1 focus:ring-primary py-2 px-3 text-navy-custom dark:text-slate-200">
                            <option>2022</option>
                            <option>2020</option>
                            <option>2018</option>
                        </select>
                    </div>
                </div>
                <div className="bg-primary/5 dark:bg-primary/10 p-4 rounded-xl border border-primary/10 dark:border-primary/20">
                    <div className="flex items-center gap-2 text-primary text-[10px] font-bold uppercase tracking-wide">
                        <span className="material-symbols-outlined text-[16px]">verified</span>
                        Votos Obtidos
                    </div>
                    <div className="flex items-baseline justify-between gap-2 mt-1">
                        <span className="text-navy-custom dark:text-white text-3xl font-black">{votos.toLocaleString('pt-BR')}</span>
                        {percentualVariacao !== 0 && (
                             <span className="text-primary text-[10px] font-bold px-2 py-0.5 bg-primary/10 rounded-full border border-primary/20">
                                {percentualVariacao > 0 ? '+' : ''}{percentualVariacao.toFixed(1)}% vs {ano === 2022 ? 2018 : 'Anterior'}
                             </span>
                        )}
                    </div>
                </div>
                <div className="space-y-3">
                    <div className="flex items-center justify-between text-[11px] font-bold uppercase text-slate-400">
                        <span className="flex items-center gap-1">Gênero</span>
                    </div>
                    <div className="w-full h-2 bg-slate-100 dark:bg-slate-700 rounded-full flex overflow-hidden">
                        <div className="h-full bg-primary" style={{width: `${eleitoradoData.genero.feminino * 100}%`}}></div>
                        <div className="h-full bg-navy-custom dark:bg-slate-500" style={{width: `${eleitoradoData.genero.masculino * 100}%`}}></div>
                    </div>
                    <div className="flex justify-between text-[10px] font-bold">
                        <span className="text-primary">Feminino: {eleitoradoData.genero.feminino * 100}%</span>
                        <span className="text-navy-custom dark:text-slate-400">Masculino: {eleitoradoData.genero.masculino * 100}%</span>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default EleitoradoCard;
