
import React, { useState } from 'react';

interface LoginPageProps {
    onLogin: (email: string, role: string) => void;
}

const LoginPage: React.FC<LoginPageProps> = ({ onLogin }) => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState('');

    const handleLogin = (e: React.FormEvent) => {
        e.preventDefault();
        setIsLoading(true);
        setError('');

        // Simulação de Login baseada nos usuários de teste criados no banco
        setTimeout(() => {
            let role = 'Assessor';
            if (email.startsWith('adm')) role = 'Adm';
            else if (email.startsWith('chefe')) role = 'Chefe';
            else if (email.startsWith('gestor')) role = 'Gestor';

            // Validamos e-mail básico para o teste
            if (email.includes('@')) {
                onLogin(email, role);
            } else {
                setError('E-mail ou senha incorretos.');
            }
            setIsLoading(false);
        }, 1000);
    };

    return (
        <div style={{ minHeight: '100vh', display: 'flex', alignItems: 'center', justifyContent: 'center', backgroundColor: '#0f172a', padding: '1rem' }}>
            <div style={{ maxWidth: '400px', width: '100%', textAlign: 'center' }}>
                <h1 style={{ color: 'white', marginBottom: '1rem' }}>Portela Hub</h1>
                <div style={{ backgroundColor: '#1e293b', padding: '2rem', borderRadius: '1rem', border: '1px solid #334155' }}>
                    <h2 style={{ color: 'white', marginBottom: '1.5rem' }}>Acesse sua conta</h2>
                    <form onSubmit={handleLogin}>
                        <input
                            type="email"
                            placeholder="E-mail"
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            style={{ width: '100%', padding: '0.75rem', marginBottom: '1rem', borderRadius: '0.5rem', border: '1px solid #334155', backgroundColor: '#0f172a', color: 'white' }}
                        />
                        <input
                            type="password"
                            placeholder="Senha"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            style={{ width: '100%', padding: '0.75rem', marginBottom: '1.5rem', borderRadius: '0.5rem', border: '1px solid #334155', backgroundColor: '#0f172a', color: 'white' }}
                        />
                        {error && <p style={{ color: '#ef4444', marginBottom: '1rem' }}>{error}</p>}
                        <button
                            type="submit"
                            disabled={isLoading}
                            style={{ width: '100%', padding: '0.75rem', borderRadius: '0.5rem', backgroundColor: '#3b82f6', color: 'white', fontWeight: 'bold', border: 'none', cursor: 'pointer' }}
                        >
                            {isLoading ? 'Entrando...' : 'Entrar'}
                        </button>
                    </form>
                    <div style={{ marginTop: '1.5rem', fontSize: '0.75rem', color: '#94a3b8' }}>
                        <p>adm@portelahub.com (ADM)</p>
                        <p>chefe@portelahub.com (CHEFE)</p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default LoginPage;
