
import { mockMunicipios } from '../data/mockData';
import { mockLiderancas } from '../data/mockLiderancas';
import { mockAssessores } from '../data/mockAssessores';
import { mockAgenda } from '../data/mockAgenda';
import { MunicipioDetalhado, Lideranca, Assessor, EventoAgenda } from '../types';

const SIMULATED_DELAY = 750; // ms

// --- Municípios ---
export const getMunicipios = (): Promise<MunicipioDetalhado[]> => {
    return new Promise(resolve => {
        setTimeout(() => {
            resolve(mockMunicipios);
        }, SIMULATED_DELAY);
    });
};

export const getMunicipioById = (id: string): Promise<MunicipioDetalhado | undefined> => {
     return new Promise(resolve => {
        setTimeout(() => {
            const municipio = mockMunicipios.find(m => m.id === id);
            resolve(municipio);
        }, SIMULATED_DELAY);
    });
};

// --- Lideranças ---
export const getLiderancas = (): Promise<Lideranca[]> => {
    return new Promise(resolve => {
        setTimeout(() => {
            resolve(mockLiderancas);
        }, SIMULATED_DELAY);
    });
};

// --- Assessores ---
export const getAssessores = (): Promise<Assessor[]> => {
    return new Promise(resolve => {
        setTimeout(() => {
            resolve(mockAssessores);
        }, SIMULATED_DELAY);
    });
};

// --- Agenda ---
export const getAgendaEventos = (): Promise<EventoAgenda[]> => {
    return new Promise(resolve => {
        setTimeout(() => {
            resolve(mockAgenda);
        }, SIMULATED_DELAY);
    });
};
