
import React, { useState, useMemo, useEffect } from 'react';
import { getMunicipios } from '../services/api';
import { Municipio } from '../types';
import Loader from '../components/Loader';

interface MunicipiosPageProps {
    navigateTo: (page: string, params: { id: string }) => void;
}

const StatusBadge: React.FC<{ status: Municipio['statusAtividade'] }> = ({ status }) => {
    const styles = {
        'Consolidado': 'bg-green-100 text-green-700 dark:bg-green-900/30 dark:text-green-400',
        'Expansão': 'bg-blue-100 text-blue-700 dark:bg-blue-900/30 dark:text-blue-400',
        'Manutenção': 'bg-amber-100 text-amber-700 dark:bg-amber-900/30 dark:text-amber-400',
        'Atenção': 'bg-red-100 text-red-700 dark:bg-red-900/30 dark:text-red-400',
    };
    return <span className={`px-3 py-1 text-[9px] font-black rounded-full uppercase tracking-wider ${styles[status]}`}>{status}</span>;
}

const MunicipiosPage: React.FC<MunicipiosPageProps> = ({ navigateTo }) => {
    const [municipios, setMunicipios] = useState<Municipio[]>([]);
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const [filtro, setFiltro] = useState('');

    useEffect(() => {
        const fetchMunicipios = async () => {
            try {
                setIsLoading(true);
                const data = await getMunicipios();
                setMunicipios(data);
                setError(null);
            } catch (err) {
                setError('Falha ao carregar os municípios.');
            } finally {
                setIsLoading(false);
            }
        };
        fetchMunicipios();
    }, []);

    const municipiosFiltrados = useMemo(() => {
        return municipios.filter(m => m.nome.toLowerCase().includes(filtro.toLowerCase()));
    }, [filtro, municipios]);

    return (
        <div className="p-8">
            <div className="flex flex-wrap items-center justify-between gap-4 mb-8">
                <div>
                    <h2 className="text-navy-custom dark:text-white text-3xl font-black tracking-tight">Gestão de Municípios</h2>
                    <p className="text-slate-500 dark:text-slate-400">Explore e gerencie as informações de cada município.</p>
                </div>
                <div className="relative w-full sm:w-64">
                    <span className="material-symbols-outlined absolute left-3 top-1/2 -translate-y-1/2 text-slate-400 text-[20px]">search</span>
                    <input 
                        className="w-full pl-10 pr-4 py-2 bg-white dark:bg-slate-800 border-slate-200 dark:border-slate-700 rounded-lg text-sm focus:ring-2 focus:ring-primary/20 focus:border-primary shadow-sm" 
                        placeholder="Buscar município..." 
                        type="text"
                        value={filtro}
                        onChange={(e) => setFiltro(e.target.value)}
                    />
                </div>
            </div>

            <div className="bg-white dark:bg-slate-800 rounded-xl shadow-sm border border-slate-200 dark:border-slate-700 overflow-hidden">
                <div className="overflow-x-auto">
                    {isLoading ? <Loader /> : error ? <div className="p-8 text-center text-red-500">{error}</div> : (
                        <table className="w-full text-left">
                            <thead className="bg-slate-50/50 dark:bg-slate-900/50">
                                <tr>
                                    <th className="px-6 py-4 text-slate-400 text-[10px] font-bold uppercase tracking-widest">Município</th>
                                    <th className="px-6 py-4 text-slate-400 text-[10px] font-bold uppercase tracking-widest">Região</th>
                                    <th className="px-6 py-4 text-slate-400 text-[10px] font-bold uppercase tracking-widest text-center">Influência</th>
                                    <th className="px-6 py-4 text-slate-400 text-[10px] font-bold uppercase tracking-widest text-center">Lideranças</th>
                                    <th className="px-6 py-4 text-slate-400 text-[10px] font-bold uppercase tracking-widest text-center">Status</th>
                                </tr>
                            </thead>
                            <tbody className="divide-y divide-slate-100 dark:divide-slate-700">
                                {municipiosFiltrados.map(municipio => (
                                    <tr key={municipio.id} onClick={() => navigateTo('MunicipioDetalhes', { id: municipio.id })} className="hover:bg-slate-50/50 dark:hover:bg-slate-700/30 transition-colors cursor-pointer">
                                        <td className="px-6 py-4 font-bold text-navy-custom dark:text-white">{municipio.nome}</td>
                                        <td className="px-6 py-4 text-slate-600 dark:text-slate-300">{municipio.regiao}</td>
                                        <td className="px-6 py-4">
                                             <div className="w-full h-2 bg-slate-100 dark:bg-slate-700 rounded-full"><div className="h-full bg-primary rounded-full" style={{width: `${municipio.influencia}%`}}></div></div>
                                        </td>
                                        <td className="px-6 py-4 text-center font-bold text-navy-custom dark:text-white">{municipio.liderancasAtivas}</td>
                                        <td className="px-6 py-4 text-center">
                                           <StatusBadge status={municipio.statusAtividade} />
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    )}
                </div>
            </div>
        </div>
    );
};

export default MunicipiosPage;
